import React, { useState, useEffect, useCallback, useRef } from 'react';
import { getBudgets, addBudget } from '../services/api';

interface Budget {
  _id: string;
  category: string;
  amount: number;
  spent: number; // For simplicity, let's assume this is tracked on frontend or via backend stats
  user: string;
  createdAt: string;
  period: 'monthly' | 'yearly';
  startDate: string;
  endDate: string;
}

interface BudgetsProps {
  showNotification: (message: string, type: 'success' | 'error' | 'info') => void;
}

const Budgets: React.FC<BudgetsProps> = ({ showNotification }) => {
  const [budgets, setBudgets] = useState<Budget[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [refreshTrigger, setRefreshTrigger] = useState(0); // New state for triggering refresh
  const isMounted = useRef(true); // Create a ref to track mount status

  // For new budget form
  const [newBudgetCategory, setNewBudgetCategory] = useState('');
  const [newBudgetAmount, setNewBudgetAmount] = useState<number | string>('');
  const [newBudgetPeriod, setNewBudgetPeriod] = useState<'monthly' | 'yearly'>('monthly'); // Default to monthly
  const [newBudgetStartDate, setNewBudgetStartDate] = useState('');
  const [newBudgetEndDate, setNewBudgetEndDate] = useState('');
  const [addLoading, setAddLoading] = useState(false); // Loading state for add budget form

  const fetchBudgets = useCallback(async (signal?: AbortSignal) => {
    try {
      if (isMounted.current) setLoading(true); // Only set loading if component is mounted
      const data = await getBudgets(signal);
      if (isMounted.current) setBudgets(data); // Only set budgets if component is mounted
    } catch (err: any) {
      // Check for AbortError or if the error message explicitly indicates cancellation
      if (err.name === 'AbortError' || (err.message && err.message.includes('canceled'))) {
        console.log('Fetch aborted or canceled');
        // Do not set error or show notification for aborted/canceled requests
        return;
      }
      console.error('獲取預算記錄失敗:', err.response?.data || err.message);
      const errorMessage = err.response?.data?.message || '無法加載預算記錄。';
      if (isMounted.current) setError(errorMessage); // Only set error if component is mounted
      showNotification(errorMessage, 'error');
    } finally {
      if (isMounted.current) setLoading(false); // Only set loading if component is mounted
    }
  }, [showNotification]);

  useEffect(() => {
    const controller = new AbortController();
    fetchBudgets(controller.signal);

    return () => {
      controller.abort();
      isMounted.current = false; // Set to false when component unmounts
    };
  }, [fetchBudgets, refreshTrigger]); // Add refreshTrigger to dependencies

  const handleAddBudget = async (e: React.FormEvent) => {
    e.preventDefault();
    setAddLoading(true);
    setError(null);

    if (!newBudgetCategory.trim() || !newBudgetAmount || Number(newBudgetAmount) <= 0 || !newBudgetStartDate || !newBudgetEndDate) {
      const errorMessage = '所有欄位都必須有效。';
      setError(errorMessage);
      showNotification(errorMessage, 'error');
      setAddLoading(false);
      return;
    }

    const controller = new AbortController();
    try {
      const budgetData = {
        category: newBudgetCategory,
        amount: Number(newBudgetAmount),
        period: newBudgetPeriod,
        startDate: new Date(newBudgetStartDate).toISOString(), // Convert to ISO string
        endDate: new Date(newBudgetEndDate).toISOString(),     // Convert to ISO string
      };
      await addBudget(budgetData, controller.signal);
      showNotification('預算新增成功！', 'success');
      if (isMounted.current) { // Only update state if mounted
        setNewBudgetCategory('');
        setNewBudgetAmount('');
        setNewBudgetStartDate('');
        setNewBudgetEndDate('');
        setRefreshTrigger(prev => prev + 1); // Trigger a refresh
      }
    } catch (err: any) {
      if (err.name === 'AbortError') {
        console.log('Add budget aborted');
        return;
      }
      console.error('新增預算失敗:', err.response?.data || err.message);
      const errorMessage = err.response?.data?.message || '新增預算失敗，請重試。';
      if (isMounted.current) setError(errorMessage); // Only set error if component is mounted
      showNotification(errorMessage, 'error');
    } finally {
      if (isMounted.current) setAddLoading(false); // Only set loading if component is mounted
    }
  };

  if (loading) {
    return <div className="text-center text-gray-400 mt-10">載入中...</div>;
  }

  if (error) {
    return <div className="text-center text-red-500 mt-10">錯誤: {error}</div>;
  }

  return (
    <div className="container mx-auto p-6 mt-8">
      <h1 className="text-3xl font-bold text-gray-50 mb-6">您的預算</h1>

      <div className="card bg-gray-800 p-6 rounded-lg shadow-xl mb-8">
        <h2 className="text-xl font-semibold text-gray-300 mb-4">新增預算</h2>
        <form onSubmit={handleAddBudget} className="space-y-4">
          {/* {error && <p className="text-red-500 text-sm mb-2">{error}</p>} */}
          {/* {success && <p className="text-green-500 text-sm mb-2">{success}</p>} */}
          <div>
            <label htmlFor="budget-category" className="block text-sm font-medium text-gray-300">類別</label>
            <input
              id="budget-category"
              name="category"
              type="text"
              required
              className="input-field"
              placeholder="預算類別 (例如：餐飲)"
              value={newBudgetCategory}
              onChange={(e) => setNewBudgetCategory(e.target.value)}
              disabled={addLoading}
            />
          </div>
          <div>
            <label htmlFor="budget-amount" className="block text-sm font-medium text-gray-300">金額</label>
            <input
              id="budget-amount"
              name="amount"
              type="number"
              step="0.01"
              required
              className="input-field"
              placeholder="預算金額 (例如：500.00)"
              value={newBudgetAmount}
              onChange={(e) => setNewBudgetAmount(e.target.value)}
              disabled={addLoading}
            />
          </div>
          <div>
            <label htmlFor="budget-period" className="block text-sm font-medium text-gray-300">週期</label>
            <select
              id="budget-period"
              name="period"
              required
              className="input-field"
              value={newBudgetPeriod}
              onChange={(e) => setNewBudgetPeriod(e.target.value as 'monthly' | 'yearly')}
              disabled={addLoading}
            >
              <option value="monthly">每月</option>
              <option value="yearly">每年</option>
            </select>
          </div>
          <div>
            <label htmlFor="budget-startDate" className="block text-sm font-medium text-gray-300">開始日期</label>
            <input
              id="budget-startDate"
              name="startDate"
              type="date"
              required
              className="input-field"
              value={newBudgetStartDate}
              onChange={(e) => setNewBudgetStartDate(e.target.value)}
              disabled={addLoading}
            />
          </div>
          <div>
            <label htmlFor="budget-endDate" className="block text-sm font-medium text-gray-300">結束日期</label>
            <input
              id="budget-endDate"
              name="endDate"
              type="date"
              required
              className="input-field"
              value={newBudgetEndDate}
              onChange={(e) => setNewBudgetEndDate(e.target.value)}
              disabled={addLoading}
            />
          </div>
          <button
            type="submit"
            className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-darkPrimary-600 hover:bg-darkPrimary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-darkPrimary-500"
            disabled={addLoading}
          >
            {addLoading ? '新增中...' : '新增預算'}
          </button>
        </form>
      </div>

      <h2 className="text-2xl font-bold text-gray-50 mb-4">所有預算</h2>
      {budgets.length === 0 ? (
        <p className="text-gray-400 text-center">目前沒有預算記錄。</p>
      ) : (
        <div className="overflow-x-auto card bg-gray-800 p-4 rounded-lg shadow-xl">
          <table className="min-w-full divide-y divide-gray-700">
            <thead className="bg-gray-700">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">類別</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">預算金額</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">已使用</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">剩餘</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">進度</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">週期</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">開始日期</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">結束日期</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-700">
              {budgets.map((budget) => {
                const spent = budget.spent || 0; // Ensure spent is a number, default to 0
                const remaining = budget.amount - spent;
                const percentage = (spent / budget.amount) * 100;
                const progressBarColor = percentage > 90 ? 'bg-red-500' : percentage > 70 ? 'bg-yellow-500' : 'bg-green-500';

                return (
                  <tr key={budget._id} className="hover:bg-gray-700">
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-400">
                      {budget.category}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-400">
                      ${budget.amount.toFixed(2)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-400">
                      ${spent.toFixed(2)}
                    </td>
                    <td className={`px-6 py-4 whitespace-nowrap text-sm ${remaining < 0 ? 'text-red-400' : 'text-green-400'}`}>
                      ${remaining.toFixed(2)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm">
                      <div className="w-full bg-gray-700 rounded-full h-2.5">
                        <div className={`${progressBarColor} h-2.5 rounded-full`} style={{ width: `${Math.min(100, percentage)}%` }}></div>
                      </div>
                      <span className="text-xs text-gray-400 mt-1 block">
                        {percentage.toFixed(1)}%
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-400">
                      {budget.period === 'monthly' ? '每月' : '每年'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-400">
                      {new Date(budget.startDate).toLocaleDateString()}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-400">
                      {new Date(budget.endDate).toLocaleDateString()}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default Budgets; 